<template>
  <PopUp :flow='xzzhStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closeXzzh'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe630;</i>
          <span class="title-span">{{$t("account.xzry")}}</span>
        </div>
        <div class="xz-content">
          <div class="form">
            <div class="form-item">
              <div class="form-item-left">
                <em>*</em>
                <span>{{$t("account.zh")}}：</span>
              </div>
              <div class="form-item-right form-item-right1">
               <el-input v-model="newUserInfo.account" class='select' :placeholder="$t('account.tips.srzh')"></el-input>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-left">
                <em>*</em>
                <span>{{$t("account.password")}}：</span>
              </div>
              <div class="form-item-right form-item-right1">
                <el-input v-model="newUserInfo.password" type='password' class='select' :placeholder="$t('account.tips.srmm')"></el-input>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-left">
                <em>*</em>
                <span>{{$t("account.confirm")}}：</span>
              </div>
              <div class="form-item-right form-item-right1">
                <el-input v-model="newUserInfo.confirm" type='password' class='select' :placeholder="$t('account.tips.qrmm')"></el-input>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-left">
                <em>*</em>
                <span>{{$t("account.name")}}：</span>
              </div>
              <div class="form-item-right form-item-right1">
                <el-input v-model="newUserInfo.name" class='select' :placeholder="$t('account.tips.srxm')"></el-input>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-left">
                <em>*</em>
                <span>{{$t("account.phone")}}：</span>
              </div>
              <div class="form-item-right form-item-right1">
                <el-input v-model="newUserInfo.phone" class='select' :placeholder="$t('account.tips.srsj')"></el-input>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-left">
                <span>{{$t("account.gw")}}：</span>
              </div>
              <div class="form-item-right form-item-right1">
                <el-input v-model="newUserInfo.post" class='select' :placeholder="$t('account.tips.srgw')"></el-input>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-left">
                <em>*</em>
                <span>{{$t("account.sex")}}：</span>
              </div>
              <div class="form-item-right form-item-right1">
                <el-radio-group v-model="newUserInfo.sex">
                  <el-radio :label="1">{{$t("account.male")}}</el-radio>
                  <el-radio :label="0">{{$t("account.female")}}</el-radio>
                </el-radio-group>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-left">
                <em>*</em>
                <span>{{$t("account.ks")}}：</span>
              </div>
              <div class="form-item-right form-item-right1">
                <el-select class='select' style='width:133px;' v-model='newUserInfo.departmentId' filterable :placeholder="$t('message.select')">
                 <el-option
                   v-for="item in departmentList"
                   :key="item.departmentId"
                   :label="item.name"
                   :value="item.departmentId">
                 </el-option>
               </el-select>
              </div>
            </div>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='addZh'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='closeXzzh'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import { addAccount } from '@/api/account'
var that= {};
  export default {
    name: 'ghsb',
      data(){
          return {
            userInfo:this.$store.state.login.userInfo,
            newUserInfo:{
              account:'',
              password:'',
              confirm:'',
              name:'',
              phone:'',
              post:'',
              sex:1,
              departmentId:'',
            },
            accountList:[],
          }
      },
      props: {
          departmentList: {
              type: Array,
              default: function () {return []}
          },
          xzzhStatus:{
            type:Boolean
          },
          getAccountListFn: {
            type: Function,
            default: null
          },
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){

      },
      watch:{
        departmentList(){
          // that.newUserInfo.departmentId = that.departmentList[0].departmentId
        }

      },
      methods:{
        /*新增账号*/
        addZh(){
          var  re = /^1\d{10}$/;
          if(that.newUserInfo.account==''){
            that.error(that.$t('account.tips.srzh'))
          }else if(that.newUserInfo.account.length>18){
            that.error(that.$t('account.tips.zhgs'))
          }else if(that.newUserInfo.password==''){
            that.error(that.$t('account.tips.srmm'))
          }else if(that.newUserInfo.password.length>12 || that.newUserInfo.password.length<6){
            that.error(that.$t('account.tips.mmgs'));
          }else if(that.newUserInfo.confirm==''){
            that.error(that.$t('account.tips.qrmm'))
          }else if(that.newUserInfo.confirm!=that.newUserInfo.password){
            that.error(that.$t('account.tips.mmbf'))
          }else if(that.newUserInfo.name==''){
            that.error(that.$t('account.tips.srxm'))
          }else if(that.newUserInfo.phone==''){
            that.error(that.$t('account.tips.srsj'))
          }else if(!re.test(that.newUserInfo.phone)){
            that.error(that.$t('account.tips.sjgs'))
          }else if(that.newUserInfo.departmentId==''){
            that.error(that.$t('account.tips.xzks'))
          }else{
            var data={
              account:that.newUserInfo.account,
              password:that.newUserInfo.password,
              name:that.newUserInfo.name,
              phone:that.newUserInfo.phone,
              post:that.newUserInfo.post,
              sex:that.newUserInfo.sex,
              departmentId:that.newUserInfo.departmentId,
            }
            addAccount(data).then((res)=>{
              if(res.code==200 && res.success){
                that.newUserInfo = {
                  account:'',
                  password:'',
                  confirm:'',
                  name:'',
                  phone:'',
                  post:'',
                  sex:'',
                  departmentId:'',
                },
                that.$emit('getxzzhStatus',!that.xzzhStatus);
                that.success(that.$t('message.OperSuccess'));
                that.getAccountListFn();
              }
            })
          }
        },
        /*弹窗消失*/
        closeXzzh(){
          that.$emit('getxzzhStatus',!that.xzzhStatus);
        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
<style scoped lang="scss">
.xz-content{
  margin:0 42px;
  display:flex;
  flex-direction: row;
  .form{
    width:612px;
    padding-bottom:20px;
    .form-item{
      display:flex;
      height:36px;
      margin-top:20px;
      .form-item-left{
        width:100px;
        display:flex;
        justify-content: flex-end;
        em{
          font-style: normal;
          color:#E02020;
        }
        span{
          float:right;
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:600;
          color:#333;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
      .form-item-right{
        width:469px;
        span{
          float:left;
          line-height:36px;
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:#666666;
        }
        .ysbh{
          color:#1890FF;
        }
        .select{
          width:100%;
          float:left;
        }
      }

    }
  }
}
.form-item-right /deep/ .el-radio-group{
  float:left;
  margin-top:8px;
}
.form-item-right /deep/ .el-radio__inner{
  width:20px;
  height:20px;
}
.form-item-right /deep/ .el-radio__label{
  font-size:16px;
  line-height:22px;
}
.form-item-right /deep/ .el-input__inner{
  height:36px;
}
</style>



