<template>
  <PopUp :flow='xgzhStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closeXgzh'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe630;</i>
          <span class="title-span">{{$t("account.zgxx")}}</span>
        </div>
        <div class="xz-content">
          <div class="form">
            <div class="form-item">
              <div class="form-item-left">
                <span>{{$t("account.zh")}}：</span>
              </div>
              <div class="form-item-right form-item-right1">
                <span>{{currentAccount.account}}</span>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-left">
                <span>{{$t("account.name")}}：</span>
              </div>
              <div class="form-item-right">
                <span>{{currentAccount.name}}</span>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-left">
                <span>{{$t("account.phone")}}：</span>
              </div>
              <div class="form-item-right">
                <span>{{currentAccount.phone}}</span>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-left">
                <span>{{$t("account.sex")}}：</span>
              </div>
              <div class="form-item-right">
                <span v-if='currentAccount.sex==1'>{{$t("account.male")}}</span>
                <span v-if='currentAccount.sex==0'>{{$t("account.female")}}</span>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-left">
                <span>{{$t("account.gw")}}：</span>
              </div>
              <div class="form-item-right">
                <el-input v-model="putUserInfo.post" class='select' :placeholder="$t('account.tips.srgw')"></el-input>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-left">
                <em>*</em>
                <span>{{$t("account.ks")}}：</span>
              </div>
              <div class="form-item-right">
                <el-select class='select' style='width:133px;' v-model='putUserInfo.departmentId' filterable :placeholder="$t('message.select')">
                 <el-option
                   v-for="item in departmentList"
                   :key="item.departmentId"
                   :label="item.name"
                   :value="item.departmentId">
                 </el-option>
               </el-select>
              </div>
            </div>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='putZh'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='closeXgzh'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import { putAccount } from '@/api/account'

var that= {};
  export default {
    name: 'xgzh',
      data(){
          return {
            putUserInfo:{
              post:'',
              departmentId:'',
              id:''
            },
          }
      },
      props: {
          departmentList: {
              type: Array,
              default: function () {return []}
          },
          xgzhStatus:{
            type:Boolean
          },
          getAccountListFn : {
            type: Function,
            default: null
          },
          currentAccount:{
            type: Object,
          }
      },
      watch:{

      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){
        that.putUserInfo.departmentId = that.currentAccount.departmentId;
        that.putUserInfo.post = that.currentAccount.post;
      },
      methods:{
        /*修改确认*/
        putZh(){
          console.log(that.putUserInfo.departmentId)
          if(that.putUserInfo.departmentId==''){
            that.error(that.$t("tips.xzks"))
          }else{
            var data={
              id:that.currentAccount.id,
              post:that.putUserInfo.post,
              departmentId:that.putUserInfo.departmentId
            }
            putAccount(data).then((res)=>{
              if(res.code==200 && res.success){
                that.success(that.$t('message.OperSuccess'));
                that.$emit('getxgzhStatus',!that.xgzhStatus);
                that.getAccountListFn();
                that.putUserInfo = {
                  post:'',
                  departmentId:'',
                  id:''
                }
              }
            })
          }

        },
        /*关闭弹窗*/
        closeXgzh(){
          that.$emit('getxgzhStatus',!that.xgzhStatus);
        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
<style scoped lang="scss">
.xz-content{
  margin:0 42px;
  display:flex;
  flex-direction: row;
  .form{
    width:612px;
    padding-bottom:20px;
    .form-item{
      display:flex;
      height:36px;
      margin-top:20px;
      .form-item-left{
        width:130px;
        display:flex;
        justify-content: flex-end;
        em{
          font-style: normal;
          color:#E02020;
        }
        span{
          float:right;
          // display:block;
          // line-height:36px;
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:600;
          color:#333;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
      .form-item-right{
        width:469px;
        span{
          float:left;
          line-height:36px;
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:#666666;
          margin-left: 15px;
        }
        .ysbh{
          color:#1890FF;
        }
        .select{
          width:100%;
          float:left;
        }
      }

    }
  }
}
.form-item-right /deep/ .el-radio-group{
  float:left;
  margin-top:8px;
}
.form-item-right /deep/ .el-radio__inner{
  width:20px;
  height:20px;
}
.form-item-right /deep/ .el-radio__label{
  font-size:16px;
  line-height:22px;
}
.form-item-right /deep/ .el-input__inner{
  height:36px;
}
</style>



