import service from '../utils/http'
/*获取账号列表*/
export function getAccountList(data) {
  return service({
    url: '/manage/manager',
    method: 'get',
    params:data
  })
}
/*获取科室列表*/
export function getDepartmentList(data) {
  return service({
    url: '/hospital/departments',
    method: 'get',
    params:data
  })
}
/*新增账号*/
export function addAccount(data) {
  return service({
    url: '/manage/manager',
    method: 'post',
    data:data
  })
}
/*修改账号*/
export function putAccount(data) {
  return service({
    url: '/manage/manager',
    method: 'put',
    data:data
  })
}
/*删除账号*/
export function deleteAccount(data) {
  return service({
    url: '/manage/manager',
    method: 'delete',
    data:data
  })
}



