<template>
  <div class="account-all">
    <img src="../assets/img/bg.png" alt="" class='bg'>
    <Topbar>
      <i class='iconfont'>&#xe62d;</i>
      <span>{{$t("account.title")}}</span>
    </Topbar>
    <div class="account-main">
      <div class="account-choose">
        <div class="account-row">
          <div class="row-left">
            <span class="choose-title">{{$t("account.ksxz")}}:</span>
            <el-select class='select' v-model='currentDepartmentId' filterable :placeholder="$t('message.select')" @change='selectDepart'>
             <el-option
               v-for="item in departmentList"
               :key="item.departmentId"
               :label="item.name"
               :value="item.departmentId">
             </el-option>
           </el-select>
          </div>
          <div class="row-right">
            <div class="buttons">
              <a href="javascript:void(0)" @click='xzzhStatus = !xzzhStatus'>{{$t("account.xzry")}}</a>
            </div>
          </div>
        </div>
      </div>
      <div class="main">
        <el-table :data="accountList" style="width: 100%" :empty-text="$t('message.noData')">
          <el-table-column prop="name" :label="$t('account.name')" width="100"></el-table-column>
          <el-table-column prop="departmentName" :label="$t('account.szks')"></el-table-column>
          <el-table-column prop="post" :label="$t('account.gw')"></el-table-column>
          <el-table-column prop="sex" :label="$t('account.sex')">
            <template slot-scope="scope">
              <span v-if='accountList[scope.$index].sex==0'>{{$t("account.female")}}</span>
              <span v-if='accountList[scope.$index].sex==1'>{{$t("account.male")}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="phone" :label="$t('account.phone')"></el-table-column>
          <el-table-column prop="account" :label="$t('account.dlzh')"></el-table-column>
          <el-table-column prop="loginTime" :label="$t('account.last')">
          </el-table-column>
          <el-table-column prop="operation" :label="$t('account.operation')">
            <template slot-scope="scope">
              <span style='margin-right:32px;cursor:pointer;color:rgb(0, 145, 255);' @click='putZhFn(scope.row)'>{{$t("account.edit")}}</span>
              <span style='cursor: pointer;color:rgb(234, 72, 73)' @click='deleteZh(scope.row)'>{{$t("account.delete")}}</span>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-pagination
        background
        :page-size='pageSize'
        :current-page='currentPage'
        @current-change="handleCurrentChange"
        layout="prev, pager, next"
        :total="accountData.records">
      </el-pagination>
    </div>
    <xzzh :xzzhStatus='xzzhStatus' v-if='xzzhStatus' @getxzzhStatus='getxzzhStatus' :departmentList='departmentList' :getAccountListFn='getAccountListFn'></xzzh>
    <xgzh :xgzhStatus='xgzhStatus' v-if='xgzhStatus' @getxgzhStatus='getxgzhStatus' :currentAccount='currentAccount' :departmentList='departmentList' :getAccountListFn='getAccountListFn'></xgzh>
  </div>
</template>
<script>
import { mapState,mapActions} from 'vuex'
import xzzh from "../components/account/xzzh.vue"
import xgzh from "../components/account/xgzh.vue"
import Topbar from '../components/topbar'
import { getAccountList,getDepartmentList,deleteAccount } from '@/api/account'
var that = {};
export default {
  name: 'account',
  data () {
    return {
      xzzhStatus:false,
      xgzhStatus:false,
      userInfo:{},
      accountList:[],
      departmentList:that.$store.state.monitor.departmentList,
      currentDepartmentId:null,
      currentAccount:{},
      pageSize:that.global.pageSize,
      currentPage:that.global.currentPage,
      accountData:[],
    }
  },
  beforeCreate(){
      that = this;
  },
  mounted:function(){
    that.userInfo = this.$store.state.login.userInfo;
    console.log(that.userInfo)
    that.getDepartmentListFn();

  },
  methods:{
    handleCurrentChange(value){
      console.log(value)
      that.currentPage = value;
      that.getAccountListFn();
    },
    putZhFn(row){
      console.log(row)
      that.currentAccount = row;
      that.xgzhStatus = !that.xgzhStatus;
    },
    getxzzhStatus(xzzhStatus){
      that.xzzhStatus = xzzhStatus;
    },
    getxgzhStatus(xgzhStatus){
      that.xgzhStatus = xgzhStatus;
    },
    /*获取账号列表*/
    getAccountListFn(){
      var data = {
        role:that.userInfo.role,
        departmentId:that.currentDepartmentId,
        page:that.currentPage,
        pageSize:that.global.pageSize
      }
      getAccountList(data).then((res)=>{
        if(res.code==200 && res.success){
          that.accountData = res.data;
          that.accountList = res.data.list;
        }
      })
    },
    /*获取科室列表*/
    getDepartmentListFn(){
      getDepartmentList().then((res)=>{
        if(res.code==200 && res.success){
          that.departmentList = res.data;
          that.currentDepartmentId = that.departmentList[0].departmentId;
          that.getAccountListFn();
        }
      })
    },
    /*更换科室*/
    selectDepart(departmentId){
      that.currentDepartmentId = departmentId;
      that.currentPage = that.global.currentPage;
      that.getAccountListFn();
    },
    /*删除账号*/
    deleteZh(row){
      console.log(row)
        this.$confirm(that.$t("account.err.qrsc")+'?', that.$t("account.err.czts")+'：', {
          confirmButtonText: that.$t("button.confirm"),
          cancelButtonText: that.$t("button.close"),
          type: 'warning',
          center: true
        }).then(() => {
          var data={
            Id:row.id
          }
          deleteAccount(data).then((res)=>{
            if(res.code==200 && res.success){
              that.success(that.$t("message.OperSuccess"));
              that.getAccountListFn();
            }
          })
        }).catch(() => {

        });
    }
  },
  components:{
    xzzh,
    xgzh,
    Topbar
  },
}
</script>
<style lang="scss">
html,body{
width:100%;
height:100%;
  .account-all{
    width:100%;
    background-size:cover;
    height:100%;
    position:relative;
    padding-top:40px;
    .account-main{
      position:relative;
      margin:0 auto;
      width:83%;
      .account-choose{
        padding-top: 47px;
        display:flex;
        flex-direction: column;
        .account-row{
          display:flex;
          flex-direction: row;
          justify-content: space-between;
          margin-bottom:23px;
          .row-left{
            width:50%;
            display:flex;
            flex-direction: row;
            .choose-title{
              font-size:18px;
              font-family:PingFangSC-Medium,PingFang SC;
              font-weight:600;
              color:rgba(0,0,0,1);
              line-height:36px;
              margin-right:7px;
              height:36px;
            }
            .choose-title1{
              margin:0 17px;
            }
          }
          .row-right{
            width:50%;
            display:flex;
            flex-direction: row;
            justify-content: flex-end;
            .buttons{
              display:flex;
              flex-direction: row;
              a{
                display:block;
                padding:0 5px;
                height:35px;
                background:rgba(24,144,255,1);
                border-radius:2px;
                font-size:14px;
                font-family:PingFangSC-Regular,PingFang SC;
                font-weight:400;
                color:rgba(255,255,255,1);
                line-height:35px;
                text-align:center;
                margin-right:12px;
              }
              a:last-of-type{
                margin-right:0;
              }
            }
          }
        }
      }
      .main th,.main td{
        text-align:center;
        .options{
          display:flex;
          flex-direction: row;
          justify-content: center;
          em{
            font-style: normal;
            width:1px;
            height:20px;
            display:block;
            margin:0 15px;
            background:#D8D8D8;
          }
          .iconfont{
            color:#000;
            font-size:22px;
          }
        }
      }
      .main th{
        font-size:16px;
        font-family:PingFangSC-Regular,PingFang SC;
        font-weight:600;
        color:rgba(0,0,0,1);
      }
      .main td{
        font-size:14px;
        font-family:PingFangSC-Regular,PingFang SC;
        font-weight:400;
        color:#A3A3A3;
      }
    }
  }
}
.main /deep/ .el-pagination{
  display:flex;
  justify-content: flex-end;
  padding:21px 0;
}
.main .el-pagination{
  display:flex;
  justify-content: flex-end;
  padding:21px 0;
  float:right;
}
</style>



